body {
  margin: 0 auto;
  padding: 0;
  font-family:sans-serif;
}
.admin{
  display: none;
}
.img-container {
    position: relative;
    margin-top: 0px; 
    padding: 0; 
    width: 100%;
    height: 92.15%;
    overflow: hidden;
    text-align: center;
}

.img-container img {
    width: 100% ;
    height: auto;
}

.img-container .btn {
  position: absolute;
  top: 60%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  background-color: rgb(160, 34, 132);
  color: white;
  font-size: 24px;
  padding:25px 100px 55px 100px;
  border: none;
  cursor: pointer;
  border-radius: 5px;
  text-align: center;
 }
  
.img-container .btn:hover {
    background-color: rgba(231, 169, 0, 0.986);
    color: black;
}
